.button{
    background: #FF3E79;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    position: relative;
    border:  none;
}

.button:hover{
    box-shadow: 0px 0px 2px 1px #FF3E79;
}

.button-before::before{
    content: '';
    display: inline-block;
    width: 2px;
    height: 35px;
    background-color: #7E7E82;
    opacity: 0.2;
    position: absolute;
    left: -18px;
}

.label-button{
    display: none;
}

.popup-window_feedback__button{
    position: fixed;
    top: 38px;
    right: 460px;
}

@media screen and (max-width: 767px){
.popup-window_feedback__button{
    position: static;
    margin-left: 20px;
    margin-bottom: 20px;
}
}

@media screen and (min-width: 768px) and (max-width: 1119px) {
.popup-window_feedback__button{
    top: 13px;
    right: 350px;
}  
}

@media screen and (min-width: 1120px) and (max-width: 1439px){
.label-button{
    font-family: "TTLakesBold";
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    font-size: 16px;
    width: 190px;
}
}


@media screen and (min-width: 1440px){
  .button_invisible{
    display: none;
  }
    
  .label-button{
    font-family: "TTLakesBold";
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    font-size: 16px;
    width: 190px;
}
}
