@charset "UTF-8";
body {
  font-family: 'TTLakes';
  background: #f8f8f8; }

.invisible {
  display: none; }

.height {
  height: auto; }

.scroll {
  overflow: hidden; }

@media screen and (min-width: 577px) and (max-width: 1119px) {
  body {
    overflow-y: auto; }
  .invisible {
    display: flex; } }

@media screen and (min-width: 1120px) {
  body {
    background-color: #f8f8f8; }
  .invisible {
    display: inline; } }

@media screen and (min-width: 1440px) {
  body {
    display: flex; } }

/* Подключение шрифтов */
@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: 'TTLakesB';
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-style: normal;
  font-weight: normal; }
